import luButtonRipple from '@/components/lu-button-ripple/lu-button-ripple.vue';
export default {
    components: { luButtonRipple },
    data() {
        return {
            btnList:[
                {   text:"透明度：1",
                    backgroundColor:"#999",
                    opacity:1
                },
                {
                    text:"透明度：0.6",
                    backgroundColor:"#999",
                    opacity:0.6
                },
                {
                    text:"透明度：0.3",
                    backgroundColor:"#999",
                    opacity:1
                },
                {
                    text:"透明度：0.1",
                    backgroundColor:"#999",
                    opacity:1
                },
                {
                    text:"颜色：红",
                    backgroundColor:"red",
                    opacity:1
                },
                {
                    text:"颜色：黄",
                    backgroundColor:"yellow",
                    opacity:1
                },
                {
                    text:"颜色：绿",
                    backgroundColor:"green",
                    opacity:1
                },
                {
                    text:"颜色：蓝",
                    backgroundColor:"blue",
                    opacity:1
                }
            ]
        };
    },
    methods: {
        _tap(i) {
            if (i>=0) {
                console.log('通过第'+(i+1)+'个buttonRipple触发');
            }else{
                console.log('通过btn-box触发');
            }
        }
    }
};
